import React from 'react';

const FeedbackOptions = ({ feedback, onLeaveFeedback }) => {
  return (
    <>
      <button type="button" name="good" onClick={onLeaveFeedback} className="">
        Good
      </button>
      <button
        type="button"
        name="neutral"
        onClick={onLeaveFeedback}
        className=""
      >
        Neutral
      </button>
      <button type="button" name="bad" onClick={onLeaveFeedback} className="">
        Bad
      </button>
    </>
  );
};

// const FeedbackOptions = ({ options, onLeaveFeedback }) => {
//   return options.map(option => (
//     <FeedbackButton feedback={option} onLeaveFeedback={onLeaveFeedback} />
//   ));
// };

export default FeedbackOptions;
